import {isMobile} from "react-device-detect";
import "./Navbar.css";
import DropDown from "./Dropdown";
import {useState, useEffect} from "react";

function Navbar(props) {
    const [selectedValue, setSelectedValue] = useState(props.text.lang);

    useEffect(function () {
        console.log("Set Nav lang");
        console.log(props.text.lang);
        setSelectedValue(props.text.lang);

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.text.lang]);

    const handleChange = (event) => {
        const newLang = event.target.value.toLowerCase();
        if (newLang === selectedValue)
            return;
        console.log("New Lang " + newLang)
        setSelectedValue(newLang);
        props.context.updateCookie("lang=" + newLang, 999);
        window.location.reload();
    }

    return (
        <>
            <div id="navbar" className="navbar" hidden={props.text.lang !== undefined}>
                <div className='title'>
                    {isMobile? (<h1>{props.text["titleShort"]}</h1>) : (<h1>{props.text["titleLong"]}</h1>)}
                </div>
                <select className="lang" value={selectedValue} onChange={handleChange}>
                    <option className="option" value="eng"> ENG </option>
                    <option className="option" value="kor"> KOR </option>
                </select>
                <DropDown text={props.text} context={props.context} doc={props.doc}/>
            </div>
        </>
    )
}
export default Navbar;
