import React, { useEffect, useState } from "react";
import "./Page.css"
import { useNavigate } from 'react-router-dom'
import { toast } from "react-toastify";
import { toastProp } from "../Util";

function RequestBook(props) {
    const [needConfirm, setNeedConfirm] = useState(false);
    const [title, setTitle] = useState("");
    const [author, setAuthor] = useState("");
    const [publisher, setPublisher] = useState("");
    const [note, setNote] = useState("");
    const [today, setToday] = useState("");
    const navigate = useNavigate();

    useEffect(function () {
        async function initialize() {
            console.log("=======================================");
            console.log("Request book");
            setToday(new Date().toISOString().slice(0, 10))
        }

        initialize();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(
        () => {
            if (props.doc.logState === -1)
                navigate("/logIn", { state: {from: "/RequestBook"} });
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [props.doc.logState]
    );


    function setProperty(index, value)
    {
        switch (index)
        {
            case 0:
                setTitle(value);
                break;
            case 1:
                setAuthor(value);
                break;
            case 2:
                setPublisher(value);
                break;
            case 3:
                setNote(value);
                break;
            default:
                break;
        }
    }

    async function confirmAction()
    {
        setNeedConfirm(false);
        setReadOnly(false);

        const request =
            "{\n" +
            " \"user_id\": \"" + props.doc.userInfo["_id"] + "\",\n" +
            " \"title\": \"" + title + "\",\n" +
            " \"author\": \"" + author + "\",\n" +
            " \"publisher\": \"" + publisher + "\",\n" +
            " \"note\": \"" + note + "\",\n" +
            " \"date\": \"" + today + "\",\n" +
            " \"action\": \"bookRequest\"\n" +
            "}\n";
        console.log(request);

        props.context.sendRequest(request);

        const prop = toastProp;
        prop.type = toast.TYPE.SUCCESS;
        prop.autoClose = 3000;
        toast.info(props.text.regSuccess, prop);

        clear();
    }

    function cancelAction()
    {
        setNeedConfirm(false);
        setReadOnly(false);
    }

    function submit()
    {
        setNeedConfirm(true);
        setReadOnly(true);
    }

    function clear()
    {
        document.getElementById('book').value = "";
        document.getElementById('author').value = "";
        document.getElementById('publisher').value = "";
        document.getElementById('notice_').value = "";
    }

    function setReadOnly(readonly)
    {
        document.getElementById('book').readOnly = readonly;
        document.getElementById('author').readOnly = readonly;
        document.getElementById('publisher').readOnly = readonly;
        document.getElementById('notice_').readOnly = readonly;
    }

    return (
        <div id="checkOut">
            <div id="title">
                <h2>
                    {props.text.requestBook}
                </h2>
            </div>
            <table><tbody>
                <tr key="Date">
                    <td> {props.text.registeredDate} </td>
                    <td colSpan="3" id="today">
                        {today}
                    </td>
                </tr>
                <tr key="Title">
                    <td> {props.text.title} </td>
                    <td colSpan="3">
                        <input type="text" className="input" id="book" onChange={(event) => {setProperty(0, event.target.value);}} />
                    </td>
                </tr>
                <tr key="Author">
                    <td> {props.text.author} </td>
                    <td colSpan="3">
                        <input type="text" className="input" id="author" onChange={(event) => {setProperty(1, event.target.value);}} />
                    </td>
                </tr>
                <tr key="Publisher">
                    <td> {props.text.publisher} </td>
                    <td colSpan="3">
                        <input type="text" className="input" id="publisher" onChange={(event) => {setProperty(2, event.target.value);}} />
                    </td>
                </tr>
                <tr key="Notice">
                    <td> {props.text.notice_} </td>
                    <td colSpan="3">
                        <input type="text" className="input" id="notice_" onChange={(event) => {setProperty(3, event.target.value);}} />
                    </td>
                </tr>
            </tbody></table>
           <button id="submit" onClick={async () => submit()} disabled={(title.length === 0)}> {props.text.submit} </button>
            <div id="checkRent" hidden={!needConfirm}>
                <div id="bookName"> {props.text.confirmSave} </div>
                <button id="confirm" onClick={async () => confirmAction()}> {props.text.confirm} </button>
                <button id="cancel" onClick={() => cancelAction()}> {props.text.cancel} </button>
            </div>
        </div>
    );
}

export default RequestBook;
